<template>
    <div>
        <head-top webActive="mine"></head-top>
        <div class="bgGray" id="unIndex">
            <div class="container">
                <div class="ucBg">
                    <side-bar></side-bar>
                    <div class="ucPanel ucP_Box">
                        <el-page-header @back="goBack" content="写评价">
                        </el-page-header>
                        <div class="flex f-nowrap j-bet topDes">
                            <div>
                                <span class="mr15">订单编号：{{info.order_code}}</span>
                                <span>下单时间：{{info.time}}</span>
                            </div>
                            <div>
                                <el-button size="small" class="mr10" @click="toDetail(order_id)">查看详情</el-button>
                                <span :class="info.state=='待付款'?'colorPrice stateText':info.state=='取消订单'?'color9 stateText':'colorGreen stateText'">{{info.state}}</span>
                            </div>
                        </div>
                        <div class="ePanel" >
                            <div class="ePic">
                                <el-image
                                        class="eImg"
                                        :src="info.goods[0].pic"
                                        fit="cover"
                                        :preview-src-list="Array(info.goods[0].pic)"></el-image>
                            </div>
                            <div class="eInfo">
                                <h1>{{info.goods[0].name}}</h1>
                                <p>规格：{{info.goods[0].spec_name}}{{info.goods[0].spec_unit}}</p>
                                <p>价格：￥{{info.goods[0].price}}/{{info.goods[0].supply_unit}}</p>
                                <p>数量：{{info.goods[0].number}}</p>
                            </div>
                            <div class="eBox">
                                <el-form ref="form" label-width="80px">
                                    <el-form-item class="eLine" label="评分">
                                        <el-rate class="mt10"
                                                 v-model="form.goodsRank"
                                                 show-score
                                                 text-color="#ff9900"></el-rate>
                                    </el-form-item>
                                    <el-form-item class="eLine" label="评价">
                                        <el-input
                                                type="textarea"
                                                placeholder="请输入内容"
                                                v-model="form.content"
                                                maxlength="1000"
                                                show-word-limit
                                                :autosize="{ minRows: 4, maxRows: 6}"
                                        >
                                        </el-input>
                                    </el-form-item>
                                   <el-form-item class="eLine" label="晒图">
                                       <el-upload class="picUploader"
                                                  action="http://api.sixmol.com/api/v1/uploadComment"
                                                  :limit="5"
                                                  :on-exceed="handleExceed"
                                                  list-type="picture-card"
                                                  :on-change="handleChange"
                                                  :on-preview="handlePictureCardPreview"
                                                  :on-remove="handleRemove">
                                           <i class="el-icon-plus"></i>
                                       </el-upload>
                                       <el-dialog class="tc" :visible.sync="dialogVisible">
                                           <img :src="dialogImageUrl" alt="">
                                       </el-dialog>
                                   </el-form-item>
                                </el-form>
                            </div>
                        </div>
                        <div class="tc mt30 f16">
                            <el-button type="primary" @click="confirm()">提交评价</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import sideBar from '../../../components/usercenter/sideBar';
    import headTop from '../../../components/header/headTop';
    import {addComment,uploadComment,getOrderInfo} from '../../../service/api';
    export default {
        name: 'evaluate',
        data(){
            return{
                order_id:'',
                info:'',
                form:{
                    goodsId:'',
                    speId:'',
                    content: '',
                    goodsRank:5,
                    photos:[]
                },
                fileList:[],
                dialogImageUrl: '',
                dialogVisible: false
            }
        },
        components:{
            sideBar,
            headTop
        },
        mounted(){
            this.initData();
        },
        computed:{
            
        },
        methods:{
            async initData(){
                this.order_id = this.$route.query.order_id || '';
                var res = await getOrderInfo(this.order_id);
                this.info = res;

                this.form.goodsId = res.goods[0].goods_id;
                this.form.speId = res.goods[0].spec_id;
            },
            goBack() {
                this.$router.go(-1);
            },
            handleChange(file, fileList) {
                this.calculateFileList(fileList);
            },
            handleRemove(file, fileList) {
                this.calculateFileList(fileList);
            },
            handlePictureCardPreview(file, fileList) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            handleExceed(files, fileList) {
                this.$message.warning(`当前限制只能选择上传 5 张图片`);
            },
            toDetail(order_id){
                this.$router.push({path:"/userCenter/order/detail",query:{order_id:order_id}});
            },
            async confirm(){
                if(this.form.content==''){
                    this.$message({
                        message:'评价内容不能为空',
                        type:'warning'
                    });
                }else{
                    var res = await addComment(
                        this.form.content,
                        this.form.goodsId,
                        this.form.speId,
                        this.order_id,
                        this.form.goodsRank,
                        this.form.photos
                    );
                    this.$message({
                        message:res.msg,
                        type:res.status==12000?'success':'warning'
                    });
                    var self = this;
                    setTimeout(function(){
                        self.$router.push({path:"/userCenter/order/detail",query:{order_id:self.order_id}});
                    },1000);
                }
            },
            calculateFileList(fileList){
                var arr = [];
                for(var i =0 ; i < fileList.length ; i++){
                    arr[i] = fileList[i].response;
                }
                this.form.photos = arr;
            }
        },
        created(){


        },

    }

</script>

<style>
    .topDes{
        background-color: #f0f0f0;
        padding:15px;
        margin-top:15px;
    }
    .ePanel{
        display: flex;
        flex-wrap: nowrap;
        padding:15px 0;
        border-bottom:1px solid #ddd;
    }
    .ePic{
        width:15%;
    }
    .eImg{
        width:80px;
        height: 80px;
        border:1px solid #e4e4e4;
        padding:5px;
    }
    .eInfo{
        width:35%;
        color:#333;
    }
    .eInfo p{
        color:#999;
        font-size:14px;
    }
    .eBox{
        width:50%;
    }
    .eLine{
        margin-bottom: 10px;
    }


    .picUploader .el-upload--picture-card {
        width: 62px;
        height: 62px;
        line-height: 62px;
    }

    .picUploader .el-upload-list--picture-card .el-upload-list__item {
        width: 62px;
        height: 62px;
    }

    .picUploader .el-upload-list--picture-card .el-upload-list__item-thumbnail {
        width: 62px;
        height: 62px;
    }
    .topDes{background-color: #f5f5f5;line-height: 30px;padding:5px 15px;font-size: 13px;}
    .stateText{font-size: 18px;font-weight: bold;}


</style>



